import Vue from 'vue'
import Vuex from 'vuex'
import router from '../router'
import VuexPersistence from 'vuex-persist'
import pathify from 'vuex-pathify'


const vuexLocal = new VuexPersistence({
  storage: window.localStorage
})

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    token: "",
    apiUrl: "https://monolith-319538.herokuapp.com/",
    staySignedIn: false,
    accountType: "",
    email: "",
    username: "",
    firstName: "",
    lastName: "",
    bio: "",
    pfp: "",
  },
  plugins: [vuexLocal.plugin,pathify.plugin],
  getters: {
    checkTokenNull: state => {
      if (state.token == "") {
        return true
      } else {
        return false
      }
    },
    token: state => {
      return state.token
    },
    apiUrl: state => {
      return state.apiUrl
    },

    email: state => {
      return state.email
    },
    username: state => {
      return state.username
    },
    firstName: state => {
      return state.firstName
    },
    lastName: state => {
      return state.lastName
    },
    bio: state => {
      return state.bio
    },
    pfp: state => {
      return state.pfp
    },
    staySignedIn: state => {
      return state.staySignedIn
    },
    accountType: state => {
      return state.accountType
    },
  },
  mutations: {
    SET_TOKEN (state, n) {
      state.token = n
    },
    SET_EMAIL (state, n) {
      state.email = n
    },
    SET_USERNAME (state, n) {
      state.username = n
    },
    SET_FIRSTNAME (state, n) {
      state.firstName = n
    },
    SET_LASTNAME (state, n) {
      state.lastName = n
    },
    SET_BIO (state, n) {
      state.bio = n
    },
    SET_PFP (state, n) {
      state.pfp = n
    },
    SET_STAYSIGNEDIN (state, n) {
      state.staySignedIn = n
    },
    SET_ACCOUNTTYPE (state, n) {
      state.accountType = n
    },
  },
  actions: {
    setToken ({commit},n) {
      commit('SET_TOKEN',n)
    },
    setEmail ({commit},n) {
      commit('SET_EMAIL',n)
    },
    setUsername ({commit},n) {
      commit('SET_USERNAME',n)
    },
    setFirstName ({commit},n) {
      commit('SET_FIRSTNAME',n)
    },
    setLastName ({commit},n) {
      commit('SET_LASTNAME',n)
    },
    setBio ({commit},n) {
      commit('SET_BIO',n)
    },
    setPfp ({commit},n) {
      commit('SET_PFP',n)
    },
    setStaySignedIn ({commit},n) {
      commit('SET_STAYSIGNEDIN',n)
    },
    setAccountType ({commit},n) {
      commit('SET_ACCOUNTTYPE',n)
    },


    signOut({commit, dispatch}) {
      dispatch('setToken',"");
      dispatch('setEmail',"");
      dispatch('setUsername',"");
      dispatch('setFirstName',"");
      dispatch('setLastName',"");
      dispatch('setBio',"");
      dispatch('setPfp',"")
      dispatch('setStaySignedIn',"");
      router.push('/');
    },
    handleToken({commit, dispatch}) {

    }
  },
  modules: {
  }
})
