<template>
     <div id="app">
          <router-view />
     </div>
</template>
<script>
import jwt_decode from "jwt-decode";
import axios from "axios";
export default {
     name: "app",
     methods: {
          tokenHandling() {
               if (this.$store.get("token")) {
                    let token = this.$store.get("token");
                    let decoded = jwt_decode(token);
                    let expireDate = decoded.exp;
                    const now = new Date();
                    const currentDate = Math.round(now.getTime() / 1000);
                    if (currentDate >= expireDate) {
                         this.$store.dispatch("signOut");
                    } else if (this.$store.get("staySignedIn")) {
                         axios({
                              method: "post",
                              url: this.$store.get("apiUrl") + "refresh",
                              headers: {
                                   authorization: this.$store.get("token")
                              }
                         })
                              .then(response => {
                                   this.$store.set(
                                        "token",
                                        response.data.encoded
                                   );
                              })
                              .catch(function(error) {
                                   this.$store.dispatch("signOut");
                              });
                    } else if (expireDate - currentDate <= 54000) {
                         axios({
                              method: "post",
                              url: this.$store.get("apiUrl") + "refresh",
                              headers: {
                                   authorization: this.$store.get("token")
                              }
                         })
                              .then(response => {
                                   this.$store.set(
                                        "token",
                                        response.data.encoded
                                   );
                              })
                              .catch(function(error) {
                                   console.log(error);
                              });
                    }
               }
          }
     },
     mounted() {
          this.tokenHandling();
     },
     components: {
     }
};
</script>

